.kr-dark-section {
  background: var(--panel-dark);
  color: var(--panel-dark-fg);
}
.kr-dark-section a {
  color: inherit;
}
.kr-dark-section .text-\[color\:var\(--fg\)\] {
  color: var(--panel-dark-fg);
}
.kr-dark-section .text-\[color\:var\(--muted\)\] {
  color: rgba(238, 242, 247, 0.74);
}
html[data-theme="dark"] .kr-dark-section .text-\[color\:var\(--muted\)\] {
  color: rgba(238, 242, 247, 0.74);
}
.kr-bw-section {
  background: var(--night);
  color: var(--night-fg);
}
.kr-bw-section .text-\[color\:var\(--fg\)\] {
  color: var(--night-fg);
}
