  color: rgba(238, 242, 247, 0.72);
}
@keyframes kr-wave {
  0%,
  100% {
    transform: translateY(0);
    opacity: 0.7;
  }
  50% {
    transform: translateY(-6px);
    opacity: 1;
  }
}
@keyframes kr-bar {
  0% {
    transform: translateX(-20%);
  }
  50% {
    transform: translateX(90%);
  }
  100% {
    transform: translateX(220%);
  }
}
@keyframes kr-ping {
  0% {
    transform: scale(0.75);
    opacity: 0.8;
  }
  70% {
    transform: scale(1.4);
    opacity: 0;
  }
  100% {
    transform: scale(1.4);
    opacity: 0;
  }
}
@keyframes kr-snap-in {
  0% {
    transform: translateX(-28px);
    opacity: 0;
  }
  70% {
    transform: translateX(10px);
    opacity: 1;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}
@keyframes kr-snap-out {
  0% {
    transform: translateX(0);
    opacity: 1;
  }
  100% {
    transform: translateX(-28px);
    opacity: 0;
  }
}
@keyframes kr-work-bounce-in {
  0% {
    opacity: 0.55;
    transform: translateY(140px) scale(0.95);
  }
  70% {
    opacity: 1;
    transform: translateY(-16px) scale(1.04);
  }
  100% {
    opacity: 1;
    transform: translateY(0) scale(1.03);
  }
}
.kr-work-bounce {
  animation: kr-work-bounce-in 0.85s cubic-bezier(0.16, 1.02, 0.24, 1.16) both;
}
.kr-scrollbar-none {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.kr-scrollbar-none::-webkit-scrollbar {
  width: 0;
  height: 0;
  display: none;
}
.kr-scrollbar-none::-webkit-scrollbar-thumb {
  background: transparent;
}
.kr-scrollbar-none::-webkit-scrollbar-track {
  background: transparent;
}
.kr-flip-title {
  display: inline-block;
  white-space: nowrap;
  transform-style: preserve-3d;
  perspective: 900px;
}
.kr-flip-char {
  display: inline-block;
  transform-origin: bottom center;
  opacity: 0;
  transform: rotateX(-90deg) translateY(22px);
  animation-name: kr-flip-up;
  animation-duration: 780ms;
  animation-timing-function: cubic-bezier(0.175, 0.885, 0.32, 1.275);
  animation-fill-mode: forwards;
  animation-delay: calc(18ms * var(--i));
  will-change: transform, opacity;
}
@keyframes kr-flip-up {
  0% {
    opacity: 0;
    transform: rotateX(-90deg) translateY(42px);
  }
  100% {
    opacity: 1;
    transform: rotateX(0deg) translateY(0);
  }
}
@media (prefers-reduced-motion: reduce) {
  .kr-flip-char {
    opacity: 1 !important;
    transform: none !important;
    animation: none !important;
  }
}
.kr-word-title {
  display: block;
  transform-style: preserve-3d;
  perspective: 900px;
}
.kr-word {
  display: inline-block;
  transform-origin: bottom center;
  opacity: 0;
  transform: rotateX(-78deg) translateY(22px);
  animation-name: kr-word-up;
  animation-duration: 780ms;
  animation-timing-function: cubic-bezier(0.175, 0.885, 0.32, 1.275);
  animation-fill-mode: forwards;
  animation-delay: calc(var(--i) * 70ms);
  will-change: transform, opacity;
}
@keyframes kr-word-up {
  0% {
    opacity: 0;
    transform: rotateX(-70deg) translateY(34px);
  }
  100% {
    opacity: 1;
    transform: rotateX(0deg) translateY(0);
  }
}
@media (prefers-reduced-motion: reduce) {
  .kr-word {
    opacity: 1 !important;
    transform: none !important;
    animation: none !important;
  }
}
.kr-ticker {
  overflow: hidden;
  mask-image: linear-gradient(to right, transparent, black 10%, black 90%, transparent);
  -webkit-mask-image: linear-gradient(to right, transparent, black 10%, black 90%, transparent);
}
.kr-ticker-track {
  display: flex;
  width: max-content;
  animation: kr-ticker-move 28s linear infinite;
  will-change: transform;
  transform: translateZ(0);
}
.kr-ticker:hover .kr-ticker-track {
  animation-play-state: paused;
}
@keyframes kr-ticker-move {
  0% {
    transform: translate3d(0, 0, 0);
  }
  100% {
    transform: translate3d(-50%, 0, 0);
  }
}
@media (prefers-reduced-motion: reduce) {
  .kr-ticker-track {
    animation: none !important;
    transform: none !important;
  }
}
